.blob-editor {
  flex: 1 0 100%;
  margin: 50px auto;
  background: #222222;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  background: #222222;
  color: #f5f5f5;
  font-family: "Roboto", "Arial", sans-serif;
  margin: 0;
  padding: 0;
  line-height: 1.6;
}
.blob-editor h2 {
  padding: 20px;
  margin: 0;
  background: #333333;
  text-align: center;
}
.blob-editor .blob-title {
  width: calc(100% - 40px);
  padding: 20px;
  margin: 0;
  border: none;
  background: #222222;
  color: #f5f5f5;
  font-size: 1.2em;
  border-bottom: 1px solid rgba(245, 245, 245, 0.1);
}
.blob-editor .blob-title:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(245, 245, 245, 0.2);
}
.blob-editor .wysiwyg-container {
  padding: 20px;
  background: #333333;
  border-radius: 0 0 8px 8px;
  min-height: 300px;
  overflow-y: auto;
}
.blob-editor .wysiwyg-container div[contenteditable=true] {
  color: #f5f5f5;
  background: #333333;
  border: none;
  outline: none;
  padding: 20px;
  border-radius: 8px;
  line-height: 1.8;
}
.blob-editor .wysiwyg-container div[contenteditable=true]:focus {
  box-shadow: 0 0 0 2px rgba(245, 245, 245, 0.2);
}
.blob-editor .wysiwyg-container div[contenteditable=true] p {
  margin-bottom: 1.5em;
}
.blob-editor .wysiwyg-container div[contenteditable=true] a {
  color: #4caf50;
  -webkit-text-decoration: none;
  text-decoration: none;
}
.blob-editor .wysiwyg-container div[contenteditable=true] a:hover {
  -webkit-text-decoration: underline;
  text-decoration: underline;
}
.blob-editor .wysiwyg-container div[contenteditable=true] h1,
.blob-editor .wysiwyg-container div[contenteditable=true] h2,
.blob-editor .wysiwyg-container div[contenteditable=true] h3,
.blob-editor .wysiwyg-container div[contenteditable=true] h4,
.blob-editor .wysiwyg-container div[contenteditable=true] h5,
.blob-editor .wysiwyg-container div[contenteditable=true] h6 {
  margin-top: 0;
  margin-bottom: 0.5em;
}
.blob-editor .error-message {
  color: #ff5722;
  text-align: center;
  padding: 20px;
}
.blob-editor .toolbar-container {
  --toolbar-bg-color: #f0f0f0;
  --toolbar-padding: 2px;
  --item-padding: 2px;
  --item-margin: 1px;
  --group-margin: 0 2px;
  --dropdown-x-offset: 0px;
  --toolbar-dropdown-y-offset: 0px;
  --button-bg: #fff;
  --button-text-color: #000;
  --button-font-size: 12px;
  --icon-size: 20px;
  position: absolute;
  z-index: 1000;
  background: #f0f0f0;
  background: var(--toolbar-bg-color);
  border: 1px solid #ccc;
  padding: 2px;
  padding: var(--toolbar-padding);
  display: none;
  flex-wrap: wrap;
  max-width: 100vw;
}
.blob-editor .toolbar-container .toolbar-group {
  display: flex;
  border-left: 1px solid #ccc;
  margin: var(--group-margin);
  padding: var(--item-padding);
}
.blob-editor .toolbar-container .toolbar-group > * {
  margin: var(--item-margin);
  padding: var(--item-padding);
}
.blob-editor .toolbar-container .toolbar-dropdown-button {
  position: relative;
}
.blob-editor .toolbar-container .toolbar-dropdown-button .dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1001;
  background: var(--toolbar-bg-color);
  border: 1px solid #ccc;
  padding: var(--toolbar-padding);
  display: none;
  flex-direction: column;
  opacity: 0;
  transition: opacity 0.3s;
}
.blob-editor .toolbar-container .toolbar-dropdown-button .dropdown-menu.visible {
  opacity: 1;
}
.blob-editor .toolbar-container .toolbar-dropdown-button .dropdown-item {
  display: flex;
  align-items: center;
  padding: var(--item-padding);
  background: var(--button-bg);
  border: 1px solid #ddd;
  cursor: pointer;
  margin-bottom: var(--item-margin);
  font-size: var(--button-font-size);
}
.blob-editor .toolbar-container .toolbar-dropdown-button .dropdown-item img {
  width: var(--icon-size);
  height: var(--icon-size);
  margin-right: 5px;
}
.blob-editor .toolbar-button,
.blob-editor .toolbar-dropdown-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--item-padding);
  margin: var(--item-margin);
  background: var(--button-bg);
  border: 1px solid #ddd;
  cursor: pointer;
  color: var(--button-text-color);
  font-size: var(--button-font-size);
}
.blob-editor .toolbar-button img,
.blob-editor .toolbar-dropdown-button img {
  display: none;
  width: var(--icon-size);
  height: var(--icon-size);
}
.blob-editor .toolbar-button span,
.blob-editor .toolbar-dropdown-button span {
  margin-top: 2px;
  font-size: var(--button-font-size);
}
.blob-editor .toolbar-dropdown-button {
  position: relative;
}
.blob-editor .toolbar-dropdown-button span:last-child {
  font-size: 10px;
  margin-left: 5px;
}
.blob-editor [content-editable]:-moz-read-only {
  cursor: not-allowed;
}
.blob-editor [content-editable]:read-only {
  cursor: not-allowed;
}
.blob-editor .file-preview .remove-button {
  cursor: not-allowed;
}
